<template>
  <wrapper titulo="Historial de órdenes">
    <v-row  v-if="ordenes && ordenes.length > 0">
      <v-col v-if="$usuario.esPadreDeFamilia && listaAlumnos.length > 0" cols="12" class="text-center">
        <v-menu offset-y rounded="b-xl">
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" text outlined rounded style="border-width: 5px;">
              {{ alumnoSeleccionado.nombreCompleto }}<v-icon>mdi-menu-down</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item v-for="(alumno, index) in listaAlumnos" :key="index" link @click="seleccionarAlumno(alumno)">
              <v-list-item-title>{{ alumno.nombreCompleto }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-col>
      <v-col v-if="ordenesFiltradas && ordenesFiltradas.length > 0" cols="12">
        <v-timeline align-top :dense="$vuetify.breakpoint.smAndDown" style="font-family: Nunito">
          <v-timeline-item v-for="(orden, indexOrden) in ordenesFiltradas" :key="indexOrden"
                           :color="orden.estatus.titulo === 'ENTREGADA' ? 'success' : 'error'" fill-dot>
            <v-card style="border-radius: 20px">
              <v-card-title :class="'white--text ' + (orden.estatus.titulo === 'ENTREGADA' ? 'success' : 'error') ">
                {{ orden.fecha | fecha }}
                <v-spacer/>
                <span class="caption font-weight-bold">{{ orden.estatus.titulo }}</span>
              </v-card-title>
              <v-card-text class="pt-2 pb-0">
                <h3>{{ orden.cafeteria.nombre }}</h3>
                <h4>{{ orden.costo | moneda }}</h4>
              </v-card-text>
              <v-card-actions>
                <v-btn text small color="primary" @click="abrirDialogMostrarProductos(orden)">Ver productos</v-btn>
              </v-card-actions>
            </v-card>
          </v-timeline-item>
        </v-timeline>
      </v-col>

      <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
        <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
          <img src="../../assets/historial_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
        </v-col>
        <v-col cols="12" class="text-center">
          <h2 style="font-family: Nunito;" class="text--disabled">El alumno no tiene órdenes</h2>
        </v-col>
      </v-row>
    </v-row>

    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/historial_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">No tiene órdenes</h2>
      </v-col>
    </v-row>

    <dialogo v-model="dialogoMostrarProductos" title="Productos de orden">
      <v-row v-if="productosEnOrden && productosEnOrden.length > 0">
        <v-list subheader three-line style="font-family: Nunito">
          <v-list-item v-for="(producto, indexProducto) in productosEnOrden" :key="indexProducto">
            <v-list-item-content>
              <v-list-item-title class="success--text">{{ producto.producto.nombre }}</v-list-item-title>
              <v-list-item-subtitle>{{ producto.producto.descripcion }}</v-list-item-subtitle>
              <v-list-item-subtitle>{{ producto.costo | moneda }}</v-list-item-subtitle>
              <v-list-item-subtitle v-if="producto.menu && producto.menu.id" class="text--disabled">Pertenece a menú "{{ producto.menu.nombre }}"</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-row>
    </dialogo>
  </wrapper>
</template>

<script>

  import OrdenesService from "../../services/OrdenesService";
  import UsuariosService from "../../services/UsuariosService";
  export default {
        name: "HistorialOrdenes",
    components: {},
    data(){
          return{
            dialogoMostrarProductos: false,
            ordenes: [],
            listaAlumnos: [],
            alumnoSeleccionado: {},
            productosEnOrden: [],
            ordenesFiltradas: []
          }
    },
    async created() {
      this.listaAlumnos = UsuariosService.getListaAlumnos(this.$usuario)
      this.alumnoSeleccionado = this.listaAlumnos.length ? this.listaAlumnos[0] : {}
      this.$loader = true
      try {
        const getOrdenes = OrdenesService.getPromiseOrdenesById(this.$usuario)
        this.ordenes = await getOrdenes(this.$usuario.id)
      } catch (e) {
        this.ordenes = []
      }
      this.filtrarOrden()
      this.$loader = false
    },
    methods: {
      seleccionarAlumno(alumno) {
        this.alumnoSeleccionado = alumno
        this.filtrarOrden()
      },
      abrirDialogMostrarProductos(orden){
        this.productosEnOrden = orden.productos
        this.dialogoMostrarProductos = true
      },
      filtrarOrden() {
        if (this.alumnoSeleccionado && this.alumnoSeleccionado.id) {
          const vm = this
          this.ordenesFiltradas = vm.ordenes.filter(orden => {
            return (vm.alumnoSeleccionado.id === orden.alumno.id)
              && (orden.estatus && (orden.estatus.titulo === 'ENTREGADA' || orden.estatus.titulo === 'CANCELADA'))
          })
        } else
          this.ordenesFiltradas = [ ]
      },
    }
  }
</script>

<style scoped>
</style>
